const WIDTH_DATA: Record<string, number> = {
  "title,date,array": 200,
  "array-popper,tag,tags,object": 200,
  "object-string,email,string": 150,
  "status-list": 130,
  user: 200,
  switch: 80,
  date: 175,
  actions: 65,
  progress: 110,
  draggable: 26,
  avatar: 40,
  "tlp,tlp_data": 4,
  "selection-box": 30,
  expand: 0,
  default: 100
};

function columnWidth(type: string): number {
  return WIDTH_DATA[
    Object.keys(WIDTH_DATA).find((key) => key.split(",").includes(type)) ||
      "default"
  ];
}

function tlpValue(row: any, defaultValue = "None"): string {
  return row?.tlp || defaultValue;
}

export { columnWidth, tlpValue, WIDTH_DATA };
